/* Reset CSS */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


/* Body styling */

body {
  font-family: "Roboto", sans-serif;
  background-color: #f5f5f5;
  width: 100%;
  min-height: 100vh;
}


/* Container styling */

.container {
  width: 100%;
  min-height: 100vh;
  padding: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
}


/* Header styling */

.container h1 {
  font-size: 3rem;
  text-align: center;
  color: #0e65a7;
}


/* Game styling */

.container .game {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  width: 100%;
  height: 600px;
}

.player-1-container,
.player-2-container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  width: 50%;
  height: 100%;
  flex-direction: column;
  border-radius: 20px;
  overflow: hidden;
  /* padding: 10px; */
}


/* Player styling */

.player_1,
.player_2 {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 20px;
  width: 100%;
  height: 100%;
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.166);
  border-radius: 20px;
  overflow: hidden;
  border: 2px solid #000000;
}

.game-info {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 90%;
  flex-direction: column;
  gap: 20px;
}


/* Player background colors */

.player_1 {
  background-color: #0e65a7;
}

.player_2 {
  background-color: #0ea7a4;
}


/* Player name styling */

.player_1 h2,
.player_2 h2 {
  font-size: 2rem;
  text-align: center;
  color: #0e65a7;
  cursor: pointer;
  padding: 2px 10px;
  background-color: #ffffff;
  border-radius: 10px;
}


/* Result styling */

.result {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 200px;
  height: 200px;
  font-size: 6rem;
  font-weight: 200;
  color: #0e65a7;
  background-color: #f5f5f5;
  border-radius: 10px;
}


/* Button styling */

.btn1,
.reset1,
.btn2,
.reset2 {
  width: 200px;
  height: 45px;
  border-radius: 20px;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  transition: background-color 0.1s linear;
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.166);
}


/* Player-specific button styling */

.btn1,
.btn2 {
  background-color: #0e65a7;
  color: #fff;
}

.reset1,
.reset2 {
  background-color: #0e65a7;
  color: #ffffff;
}


/* Active state for buttons */

.btn:active,
.reset:active {
  background-color: #0e65a7;
  color: #f5f5f5;
}


/* Progress container styling */

.progress-container-1,
.progress-container-2 {
  width: 30px;
  height: 490px;
  background-color: #f5f5f5;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  align-self: flex-start;
  border: 1px solid #0e65a7;
  border-radius: 20px;
  box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.166);
  overflow: hidden;
}


/* Progress bar styling */

.progress-bar {
  width: 100%;
  transition: width 0.3s ease;
  transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  -o-transform: rotate(180deg);
  align-self: flex-end;
  -webkit-transition: width 0.3s ease;
  -moz-transition: width 0.3s ease;
  -ms-transition: width 0.3s ease;
  -o-transition: width 0.3s ease;
}